import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import { Panel, PanelHeader, Header, Button, Group, Cell, Div, Avatar, FormItem, Input, FormLayoutGroup } from '@vkontakte/vkui';
import { Icon24Send } from '@vkontakte/icons';
import './Home.css';
import bridge from '@vkontakte/vk-bridge';






const Home = ({ id, go, fetchedUser}) => {




	


	const list = {
		list:[
			{
				from: "anon",
				mess: "привет!",
				class: "a"
			},
			{
				from: "me",
				mess: "привет",
				class: 'b'
			},
			{
				from: "anon",
				mess: "что-то хотел?",
				class: "b"
			},
			{
				from: "anon",
				mess: "да  ",
				class: "a"
			},
			{
				from: "anon",
				mess: "я купил дошик",
				class: "a"
			},
			{
				from: "anon",
				mess: "можно начать стартап",
				class: "a"
			},
			{
				from: "me",
				mess: "как тут кинуть в чс?",
				class: 'b'
			}
		]
	}


	const [mess, setMess] = useState(null);

	function sayHello() {
		list.add({
			from: "me",
			mess: mess,
			class: 'b'
		})
    console.log('Hello!');
  }



	return (


			<Panel id={id}>
				<PanelHeader>Messenger</PanelHeader>
				{fetchedUser &&
				<Group header={<Header mode="secondary">User Data Fetched with VK Bridge</Header>}>
					<Cell
						before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
						description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
					>
						{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
					</Cell>
				</Group>}

				<Group header={<Header mode="secondary">Navigation Example</Header>}>
					<Div className = "message">
						{list.list.map(el => (
						<h1 className = {el.class}> {el.mess} </h1>





						))}

						<FormLayoutGroup mode="horizontal">

						<Input type="text" onChange={(e) => setMess(e.target.value)}placeholder="Введите сообщение" style={{ width: 1000 }}/>
						<Button onClick={sayHello} before={<Icon24Send />}>
		          </Button>
						</FormLayoutGroup>
					</Div>
				</Group>
			</Panel>

	);
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
